  font-style: italic;
  font-size: 32px;
  text-transform: uppercase;
  color: var(--color-white);
}

.divinest-light__text {
  max-width: 605px;
  line-height: 30px;
  color: var(--color-white);
}

.divinest-light__text:not(:last-child) {
  margin-bottom: 27px;
}

.projects-body {
